const postIndustry = "# Industry\r\n\r\n    POST industry\r\n\r\n## Description\r\nCreates a new industry, and returns the object created as confirmation\r\n\r\n***\r\n\r\n## Requires authentication\r\n* A valid Admin Key must be provided in the **Token** header\r\n\r\n***\r\n\r\n## Parameters\r\nRequired information:\r\n\r\n#### On body\r\n\r\n- **name** — Name of the industry\r\n\r\n***\r\n\r\n## Return format\r\nStatus code 201, along with a JSON array with the object update containing the keys and values:\r\n\r\n- **id** — id of the industry.\r\n- **industry** — name of the industry\r\n- **created_at** — date entry was created\r\n- **updated_at** — date entry was last updated\r\n\r\n***\r\n\r\n## Errors\r\n\r\n- **422 Unprocessable Entity** — Missing industry parameter\r\n\r\n***\r\n\r\n## Example\r\n**Request**\r\n\r\n    POST /api/v1/industry\r\n\r\n**Request Body**\r\n\r\n``` json\r\n{\r\n	'name': 'Sports'\r\n}\r\n```\r\n\r\n**Return**\r\n``` json\r\n[\r\n    {\r\n        'id': 97,\r\n        'industry': 'Sports',\r\n        'created_at': '2017-08-27T01:39:30.173Z',\r\n        'updated_at': '2017-08-27T01:39:30.173Z'\r\n    }\r\n]\r\n```\r\n"

const putIndustry = "# Industry\r\n\r\n    PUT industry/:id\r\n\r\n## Description\r\nUpdates an industry matching the id provided, and returns the object updated as confirmation\r\n\r\n***\r\n\r\n## Requires authentication\r\n* A valid Admin Key must be provided in the **Token** header\r\n\r\n***\r\n\r\n## Parameters\r\nRequired information:\r\n\r\n#### On path\r\n\r\n- **id** — Id of the industry\r\n\r\n#### On body\r\n\r\n- **name** — Name of the industry\r\n\r\n***\r\n\r\n## Return format\r\nStatus code 200, along with a JSON array with the object updated containing the keys and values:\r\n\r\n- **id** — id of the industry.\r\n- **industry** — name of the industry\r\n- **created_at** — date entry was created\r\n- **updated_at** — date entry was last updated\r\n\r\n***\r\n\r\n## Errors\r\n\r\n- **404 Not Found** — Industry not Found\r\n\r\n***\r\n\r\n## Example\r\n**Request**\r\n\r\n    PUT /api/v1/industry/:id\r\n\r\n**Request Body**\r\n\r\n``` json\r\n{\r\n	'name': 'Sports'\r\n}\r\n```\r\n\r\n**Return**\r\n``` json\r\n[\r\n    {\r\n        'id': 1,\r\n        'industry': 'Sports',\r\n        'created_at': '2017-08-26T17:31:39.753Z',\r\n        'updated_at': '2017-08-26T17:31:39.753Z'\r\n    }\r\n]\r\n```\r\n"

